


























import {
	mapState,
	mapMutations,	
} from 'vuex';
export default {
	computed: mapState([ 'hasLogin','userInfo']),
data(){
	return {
		
	
	items: [
		{
			pingjia_type: 1,
			name: '差评'
		},
		{
			pingjia_type: 2,
			name: '中评',
			
		},
		{
			pingjia_type: 3,
			name: '好评'
		},
	],
	current: 0,
	id:0,
	content:'',
	pingjia_type:0,
	};
},

onLoad:function(e){
		this.id = e.id;
},
methods: {	
	radioChange:function(evt) {
		this.pingjia_type= evt.target.value;
		for (let i = 0; i < this.items.length; i++) {
			if (this.items[i].value === evt.target.value) {
				this.current = i;
				break;
			}
		}
	},
	bindTextAreaBlur: function (content) {
		this.content =content.detail.value,
		console.log(content.detail.value)
	},
	submit(){
		uni.request({
			url:`${this.$serverUrl}/task/order_review?order_id=`+this.id,
			header:{
				"Content-Type": "application/x-www-form-urlencoded",
				"token":this.userInfo.token
			},
			method:"POST",
			data:{
				"pingjia_type":this.pingjia_type,
				"Content":this.content,
				},
			success:(res)=>{
				if(res.data.status==1){
					console.log('content succ')
					/* uni.navigateTo({
						url:`task?status=7`,
					}) */
				}
			}
		})
	},
},

}
